@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/screen";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Style/Layout/Position";

$height-terms-and-conditions: 500px;
$height-body-terms-and-conditions: 300px;


@mixin footerButtons {
    @include Flex.set($dir: row, $align: center, $justify: flex-end);
    @include Margin.set($top: beta, $bottom: beta);
    width: Layout.get-width(kappa);

    #{Button.$selector} {
        min-width: Layout.get-space(iota);
    }

    @include screen.lg {
        @include Margin.set($top: none, $bottom: none);
    }
}

.modalCancel {
    .modalFooter {
        @include footerButtons;
        #{Button.$selector} {
            min-width: Layout.get-space(zeta);
        }
    }
}

.modalTermsAndConditions {
    .modalBody {
        #{Text.$selector} {
            word-break: break-word;
            h1 {
                @include Font.set($align: center, $size: iota);
            }
            a {
                pointer-events: auto;
            }
        }
    }

    .modalFooter {
        @include footerButtons;
    }
}

.bottomSheetTermsAndConditions {
    max-height: $height-terms-and-conditions;
    > div:first-child {
        @include Padding.set($top: beta);
    }
    > div:last-child {
        overflow: hidden;
    }

    #{Text.$selector}.title {
        @include Margin.set($top: beta);
    }

    .bottomSheetBody {
        @include Display.set($display: block);
        max-height: $height-body-terms-and-conditions;
        overflow-y: auto;

        #{Text.$selector} {
            word-break: break-word;
            h1 {
                @include Font.set($align: center, $size: delta);
            }
            a {
                pointer-events: auto;
            }
        }
    }


    .bottomSheetFooter {
        @include footerButtons;
    }
}

.bottomSheetCancel {
    > div:first-child {
        @include Padding.set($top: beta);
    }
    #{Text.$selector}.title {
        @include Margin.set($top: beta);
        @include Color.set($fg: "gray-dark");
        word-break: break-word;
    }
    #{Text.$selector}.body {
        @include Font.set($size: delta);
        @include Margin.set($bottom: beta);
        @include Color.set($fg: "gray-dark");
        word-break: break-word;
    }
    .bottomSheetFooter {
        @include footerButtons;
        @include Margin.set($top: delta);
        #{Button.$selector} {
            min-width: Layout.get-space(zeta);
        }
    }
}

.separator {
    @include Margin.set($top: none);
    @include Position.set($position: absolute, $left: none);
}
